import React, { Component } from 'react'

import Header from '../Header/Header'
import List from '../List/List'
import Footer from '../Footer/Footer'


export default class App extends Component {

    constructor (props) {
        super(props)
        this.state = {
            listToBuy:[
                this.createNewItem('')
            ],
            totalSum: 0
        }
    }

    maxId = 0
     
    createNewItem(text) {

        return {
            price: "price",
            name: "new item",
            value: text,
            valuePrice: '',
            id: ++this.maxId,
            status: false
        }
    }

    updateState() {
        
        const newItem = {
            price: "price",
            name: "new item",
            value: '',
            valuePrice: '',
            id: this.getItemId(this.state.listToBuy),
            status: false
        };
        const oldArr = this.state.listToBuy
        const newArr = [...oldArr, newItem]

        return this.setState({ listToBuy: newArr })
    }

    getItemId(itemList) {
        if(itemList.length === 0){
            return this.maxId
        }else{
            return itemList[itemList.length - 1].id + 1
        }
        
    }

    onChangeValueName(val, id) {

        const oldArr = this.state.listToBuy
        const itemChanged = this.state.listToBuy.filter((el) => el.id === id)[0]
        itemChanged.value = val

        return this.setState({...oldArr, ...itemChanged})
    }

    onChangeValuePrice(val,id) {

        const oldArr = this.state.listToBuy
        const itemChanged = this.state.listToBuy.filter((el) => el.id === id)[0]
        itemChanged.valuePrice = val
        const priceData = this.state.listToBuy.map((item) => item.valuePrice)
        const sum = priceData.reduce((a,b) => {
            return Number(a) + Number(b)
        })

        return this.setState({...oldArr, ...itemChanged, totalSum: sum})
        
    }

    confirmItem(id) {

        const oldArr = this.state.listToBuy
        const itemConfirmed = oldArr.filter((el) => el.id === id)[0]
        itemConfirmed.status = true 

        return this.setState({...oldArr,...itemConfirmed})
    }

    editItem(id) {
        const oldArr = this.state.listToBuy
        const itemConfirmed = oldArr.filter((el) => el.id === id)[0]
        itemConfirmed.status = false 

        return this.setState({...oldArr,...itemConfirmed})
    }

    deleteItem(id) {
        const oldArr = this.state.totalSum
        const itemChanged = this.state.listToBuy.filter((el) => el.id === id)[0]
        const deletedData= this.state.listToBuy.filter((el) => el.id !== id)
        const newTotal = oldArr - Number(itemChanged.valuePrice)
        
        return this.setState({ listToBuy: deletedData, totalSum: newTotal})
        }

    render(){

        return (
            <div>
                <Header updateParent= {this.updateState.bind(this)}/>
                <List state= {this.state}
                        confirmItem= {this.confirmItem.bind(this)} 
                        deleteItem= {this.deleteItem.bind(this)}
                        onChangeValueName= {this.onChangeValueName.bind(this)}
                        onChangeValuePrice= {this.onChangeValuePrice.bind(this)}
                        editItem= {this.editItem.bind(this)}/>
                <Footer state= {this.state}/>
            </div>
        )
    }
}