import React from "react"



export default function List (props) {

        const elements = props.state.listToBuy.map( (item) => {
            if(!item.status){
                return (
                    <li key={item.id}>
                        <input type='text' placeholder= {item.name} 
                                            onChange= {(e) => props.onChangeValueName(e.target.value, item.id)}
                                            value= {item.value}>
                        </input>
                        <input type= 'number'
                        placeholder= {item.price}
                        onChange= {(e) => props.onChangeValuePrice(e.target.value, item.id)}
                        value= {item.valuePrice}>
                        </input>
                        <button onClick= {() => props.confirmItem(item.id)}>Confirm</button>
                        <button onClick= {() => props.deleteItem(item.id)}>Delete</button>
                    </li>
                )
            }else if(item.status){
                return (
                    <li key={item.id}>
                        <span>{item.value}</span>
                        <span>{item.valuePrice}</span>
                        <button onClick= {() => props.editItem(item.id)}>Edit</button>
                        <button onClick= {() => props.deleteItem(item.id)}>Delete</button>
                    </li>
                )
            }
        })
        return (
            <div>
                <ul>
                    {elements}
                </ul>
            </div>
        );

};